<template>
  <div>
   
    <div class="container">
      <div class="overlay"></div>
      <div class="banner">
        <!-- <h6>Welcome</h6> -->
        <h2>Schools of Cape Town</h2>
        <h5>Here you can view all schools in Cape Town!</h5>
      </div>
    </div>
    
    <div class="school" >
      <h2>School Table</h2>
 <AddSchool @school-added="handleSchoolAdded" />
 
 <table class="table" id="table">
  <thead>
    <tr>
      <th>#id</th>
      <th>name</th>
      <th>region</th>
    </tr>
  </thead>
  <tbody>
    <tr v-for="school in schools" :key="school.id">
      <td>{{ school.id }}</td>
      <td>{{ school.name }}</td>
      <td>{{school.region}}</td>
    </tr>
    
  </tbody>
  
</table>

    </div>
  </div>
</template>

<script>
import axios from 'axios';
import AddSchool from "../components/AddSchool.vue";

export default {
components: {
  AddSchool,
},

  data() {
    return {
      schools: []
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      const API_ROOT = process.env.VUE_APP_API_URL;
      const API_URL = `${API_ROOT}/api/school`;
      axios.get(API_URL)
        .then(response => {
          this.schools = response.data;
          console.log(this.schools)
        })
        .catch(error => {
          console.error(error);
        });
    },
    handleSchoolAdded(newSchool) {
      if (newSchool) {
        this.getData();
      }
    },
  }
};
</script>




