<template>
  <div>
  <LinkTeacher @teacher-linked="handleTeacherLinked"  :subjectID="subject.id"/>
    <br/>
    <br/>

    <h3>Teachers available for this Subject</h3>
    <ul v-for="teacher in teachers" :key="teacher.id">
      <li>{{ teacher.filter_subjects }}</li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";
import LinkTeacher from "../components/LinkTeacher.vue";

export default {
  name: "SingleSubjectView",
  components: {
    LinkTeacher,
  },
  props : ["subject"],
  data() {
    return {
      teachers: [],
    }; 
  },
 

  mounted() {
    console.log(this.subject);
    this.getData(this.subject.name);
  },

  methods: {
    getData(subject_name) {
      const API_ROOT = process.env.VUE_APP_API_URL;
      const API_URL = `${API_ROOT}/api/subject_teachers?subject_name=${subject_name}`;

      axios
      .get(API_URL)
      .then((response) => {
        if (response.data) {
          this.teachers = response.data;  
        } else {
          console.log("No data received from the API.");
        }
      })
      .catch((error) => {
        console.error(error);
      });
  },
  handleTeacherLinked(newLinkedTeacher) {
      if (newLinkedTeacher) {
        this.getData(this.subject.name);
      }
    },
   
  },
};
</script>
