<template>
  <div class="subject">
    <div class="container">
      <div class="overlay"></div>
      <div class="banner">
        <!-- <h6>Welcome</h6> -->
        <h2>Subjects in Cape Town</h2>
        <h5>Here you can view all subjects in Cape Town and the teachers that teach them!</h5>
      </div>
    </div>
    <AddSubject @subject-added="handleSubjectAdded" />
    
    <table class="table" id="table">
      <thead>
        <tr>
          <th>#id</th>
          <th>name</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="subject in subjects" :key="subject.id">
          <td>{{ subject.id }}</td>
          <td>{{ subject.name }}</td>
          <td>
            <!-- Button trigger modal -->
            <button
              type="button"
              class="btn btn-dark float-end"
              data-bs-toggle="modal"
              :data-bs-target="'#modal-' + subject.id"
            >
              VIEW MORE
            </button>

            <!-- Modal -->
            <div
              class="modal fade"
              :id="'modal-' + subject.id"
              tabindex="-1"
              aria-labelledby="exampleModalLabel"
              aria-hidden="true"
              data-bs-backdrop="true"
            >
              <div class="modal-dialog">
                <div class="modal-content">
                  <div class="modal-header">
                    <h1 class="modal-title fs-5" id="exampleModalLabel">{{subject.id}}. {{ subject.name }}</h1>
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                  </div>
                  <div class="modal-body">
                    
                    <SingleSubjectView :subject="subject" />



                  </div>
                  <div class="modal-footer">
                    <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                    <DeleteSubject/>
                  </div>
                </div>
              </div>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>


<script>
import axios from 'axios';
import SingleSubjectView from "../views/SingleSubjectView.vue";
import AddSubject from "../components/AddSubject.vue";
import DeleteSubject from "../components/DeleteSubject.vue";

export default {
  components: {
  SingleSubjectView,
  AddSubject,
  DeleteSubject,
},

  data() {
    return {
      subjects: []
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData() {

      const API_ROOT = process.env.VUE_APP_API_URL;
        const API_URL = `${API_ROOT}/api/subject`;

      axios.get(API_URL)
        .then(response => {
          this.subjects = response.data;
        })
        .catch(error => {
          console.error(error);
        });
    },
    handleSubjectAdded(newSubject) {
      if (newSubject) {
        this.getData();
      }
    }
      
    },
  
  // mounted() {
  //   this.getData();
  // },
};
</script>

