  </template>
  
  
  <script>
  import axios from 'axios';
  import SingleLearnerView from "../views/SingleLearnerView.vue";
  import AddLearner from "../components/AddLearner.vue";
  
  export default {
    components: {
    SingleLearnerView,
    AddLearner,
  },
  
    data() {
      return {
        learners: []
      };
    },
    mounted() {
      this.getData();
    },
    methods: {
      getData() {

        const API_ROOT = process.env.VUE_APP_API_URL;
        const API_URL = `${API_ROOT}/api/learner`;

        axios.get(API_URL)
          .then(response => {
            this.learners = response.data;
            console.log(this.learners)
          })
          .catch(error => {
            console.error(error);
          });
      },
      handleLearnerAdded(newLearner) {
      if (newLearner) {
        this.getData();
      }
    },
    }
  };
  </script>

