<template >
  <div >
    <LinkSubject @subject-linked="handleSubjectLinked" :teacher_id="teacher.id" />
    <br />
    <h3>Subjects available for this Teacher</h3>
    <ul v-for="subject in subjects" :key="subject.id">
      <li > {{ subject.filter_teachers }} </li>
    </ul>
  </div>
</template>

  <script>
  import axios from "axios";
  import LinkSubject from "../components/LinkSubject.vue";
  
  export default {
    name: "SingleTeacherView",
    components: {
      LinkSubject,
    },
    props: ["teacher"],
    data() {
      return {
                subjects: [],
      };
    },
  
    mounted() {
      console.log(this.teacher);
    this.getData(this.teacher.id);
    },
    methods: {
    getData(teacher_id) {
      const API_ROOT = process.env.VUE_APP_API_URL;
      const API_URL = `${API_ROOT}/api/teacher_subjects?teacher_id=${teacher_id}`;

      axios
        .get(API_URL)
        .then((response) => {
          if (response.data) {
            this.subjects = response.data;
          } else {
            console.log("No data received from the API.");
          }
        })
        .catch((error) => {
          console.error(error);
        });
    },
    handleSubjectLinked(newLinkedSubject) {
      if (newLinkedSubject) {
        this.getData(this.teacher.id);
      }
    },
  },
  };
  </script>
  