<template>
  <div>
    <h4>Select Subject to add</h4>


    <!-- <div>
      {{ teacher_id }}
      {{ selectedSubject && selectedSubject.id }}
    </div> -->


    <div class="dropdown">
      <a class="btn btn-secondary dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
        {{ selectedSubject ? selectedSubject.id + ' ' + selectedSubject.name : 'Pick a subject' }}
      </a>
    
      <ul class="dropdown-menu scrollable-dropdown"> 
        <li v-for="subject in subjects" :key="subject.id">
          <a class="dropdown-item" @click="selectSubject(subject)">{{ subject.id }} {{ subject.name }}</a>
        </li>
      </ul>
      <button type="submit" class="btn btn-primary float-end" @click="linkSubject">Link!</button>
    </div>
  </div>
</template>
  
<script>
import axios from 'axios';
import swal from 'sweetalert';

export default {
  name: "LinkSubject",
  props: {
    teacher_id: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
      subjects: [], // Initialize an empty array to store the teachers data
      selectedSubject: null, // Initialize selectedTeacher to store the selected teacher data
    };
  },
  mounted() {
    this.fetchSubjects();
  },
  methods: {
    // fetching all teachers for dropdown
    fetchSubjects() {
      const API_ROOT = process.env.VUE_APP_API_URL;
      const API_URL = `${API_ROOT}/api/subject`;

      axios.get(API_URL)
        .then(response => {
          this.subjects = response.data; // Set the teachers data received from the API
        })
        .catch(error => {
          console.error('Error fetching subjects:', error);
        });
    },

    selectSubject(subject) {
      this.selectedSubject = subject; // Update the selectedTeacher with the chosen teacher data
    },

    linkSubject() {
  if (!this.selectedSubject) {
    swal('Please select a subject to link!');
    return;
  }

  console.log('The teacher is being linked');

  // linking new teacher to subject
  const subjectData = {
    teacher_id: this.teacher_id, 
    subject_id: this.selectedSubject.id, 
  };

  console.log(subjectData);

  const API_ROOT = process.env.VUE_APP_API_URL;
  const API_URL = `${API_ROOT}/api/teachersubject`;
  console.log(API_URL);

  axios.post(API_URL, subjectData)
    .then(response => {
      console.log('Response:', response.data);
      // this.fetchTeachers();
      swal('Subject linked successfully!');
      this.selectedSubject = null; // Reset the selected teacher after successful linking
      this.$emit('subject-linked', response.data); // this.fetchData(); // Fetch the updated data      

    })
    .catch(error => {
      console.error('Error:', error);
    });
},

  },
}
</script>

<style>

.scrollable-dropdown {
  max-height: 200px; 
  overflow-y: auto; 
}
</style>