<template>
  <div>
    <h4>Select teacher to add</h4>
    <div class="dropdown">
      <a class="btn btn-secondary dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
        {{ selectedTeacher ? selectedTeacher.first_name + ' ' + selectedTeacher.last_name : 'Pick a teacher' }}
      </a>
    
      <ul class="dropdown-menu scrollable-dropdown"> 
        <li v-for="teacher in teachers" :key="teacher.id">
          <a class="dropdown-item" @click="selectTeacher(teacher)">{{ teacher.first_name }} {{ teacher.last_name }}</a>
        </li>
      </ul>
      <button type="submit" class="btn btn-primary float-end" @click="linkTeacher">Link!</button>
    </div>
  </div>
</template>
  
<script>
import axios from 'axios';
import swal from 'sweetalert';

export default {
  name: "LinkTeacher",
  props: {
    subjectID: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
      teachers: [], // Initialize an empty array to store the teachers data
      selectedTeacher: null, // Initialize selectedTeacher to store the selected teacher data
    };
  },
  mounted() {
    this.fetchTeachers();
  },
  methods: {
    // fetching all teachers for dropdown
    fetchTeachers() {
      const API_ROOT = process.env.VUE_APP_API_URL;
      const API_URL = `${API_ROOT}/api/teacher`;

      axios.get(API_URL)
        .then(response => {
          this.teachers = response.data; // Set the teachers data received from the API
        })
        .catch(error => {
          console.error('Error fetching teachers:', error);
        });
    },

    selectTeacher(teacher) {
      this.selectedTeacher = teacher; // Update the selectedTeacher with the chosen teacher data
    },

    linkTeacher() {
      if (!this.selectedTeacher) {
        swal('Please select a teacher to link!');
        return;
      }

      console.log('The teacher is being linked');

      // linking new teacher to subject
      const teacherData = {
        teacher_id: this.selectedTeacher.id, 
        subject_id: this.subjectID,
      };

      console.log(teacherData);

      const API_ROOT = process.env.VUE_APP_API_URL;
      const API_URL = `${API_ROOT}/api/teachersubject`;
      console.log(API_URL);

      axios.post(API_URL, teacherData)
        .then(response => {
          console.log('Response:', response.data);
          // this.fetchTeachers();
          swal('Teacher linked successfully!');
          this.selectedTeacher = null; // Reset the selected teacher after successful linking
          this.$emit('teacher-linked', response.data);//  this.fetchData(); // Fetch the updated data      

        })
        .catch(error => {
          console.error('Error:', error);
        });
    },
  },
}
</script>

<style>

.scrollable-dropdown {
  max-height: 200px; 
  overflow-y: auto; 
}
</style>